import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
import VueAwesomeSwiper from 'vue-awesome-swiper'

import ProductStoryDetails from '@/view/comment/comment-product-story-details.vue';// 产品故事详情

import 'html5-reset/assets/css/reset.css'
import 'swiper/dist/css/swiper.css'// require styles

Vue.config.productionTip = false
Vue.use(Router)
Vue.use(VueAwesomeSwiper, /* { default global options } */)

var router = new Router({
  routes: [
    {
      path: '/',
      name: 'product-story-details',
      component: ProductStoryDetails
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


