// John
// import AddRankCard from '@/components/add-rank-card.vue'; //加入榜单
// 缺跳转位置 缺加入收藏
<template>
  <div class="AddRankCard clearfix" v-if="vData">
    <div class="AddRankCard-avatar" :style="{backgroundImage : `url(${vData.user_profile.profile.avatar_path})`}" @click="goUser(vData.user_profile.id)"></div>
    <div class="AddRankCard-add">
      <span class="icon-editor_plus_photo_icon" @click="addFavorites()"></span>
    </div>
    <div class="AddRankCard-author" @click="goUser(vData.user_profile.id)">{{vData.user_profile.username}}</div>
    <div class="AddRankCard-time">{{created_at}}</div>
  </div>
</template>

<script>
import moment from 'moment'
import {nativeGo, nativeAddFavorites} from '@/assets/js/bridge.js'
export default {
  props:['vData'],
  data(){
    return {

    }
  },
  computed:{
    created_at(){
      return moment(this.vData.created_at).format('YYYY-MM-DD')
    }
  },
  methods:{
    goUser(_id){
      nativeGo('user',{id:_id})
    },
    addFavorites(){
      nativeAddFavorites()
    }
  }
}
</script>

<style lang="less" scoped>
@m:750/375;
.AddRankCard{}
  .AddRankCard-avatar{
    float:left;
    margin-right:10px*@m;
    width:40px*@m;height:40px*@m;
    border-radius: 50%;
    // background:skyblue;
  }
  .AddRankCard-add{
    float:right;
    width:40px*@m;height:40px*@m;
    border-radius: 50%;
    background-image: linear-gradient(-180deg, #57E6A6 0%, #3BB76E 100%);
    box-shadow: 0 0 30px*@m 0 rgba(71,246,143,0.45);
    text-align: center;
    line-height: 40px*@m;
    &>span{
      color:#FFFFFF;
      font-size: 16px*@m;
    }
  }
  .AddRankCard-author{
    float:left;
    font-family: PingFangHK-Medium;
    width:245px*@m;
    margin-top:4px*@m;
    font-size: 13px*@m;
    color: #B9B9B9;
    line-height: 13px*@m;
  }
  .AddRankCard-time{
    width:245px*@m;
    float:left;
    font-family: PingFangHK-Regular;
    margin-top:6px*@m;
    font-size: 12px*@m;
    color: #C6C6C6;
    line-height: 12px*@m;
  }
</style>



